<template>
    <div id="Software">
      <h2>MetaGene云服务</h2>
      <p>
        MetaGene云服务除了具有MetaGene压缩的所有功能外，还提供私有云服务。我们既可为您重新部署云平台也可在现有云平台上部署MetaGene压缩。前者是从硬件到软件的一站式服务，后者是在现有云平台的生物信息软件框架中进行无缝衔接，为您减少和避免重新搭建和部署云端软件框架的成本和烦恼。MetaGene私有云还支持可直接从云端流式传输，无需进行本地存储和格式改变，并且与其他生物信息软件配套实现实时分析，从而避免下载完整的压缩数据后才可解压的时间。不仅如此，通过MetaGene云服务，我们还能够对不同用户设置访问与使用的权限，保证数据得到充分的保护。      </p>
    </div>
</template>
<script>
export default {
    name: 'Software',
    data(){
        return{

        }
    }
}
</script>
